    --background: linear-gradient(to left, #f7ba2b 0%, #ea5358 100%);
}

body{
    margin: 0;
    padding: 0;
    background-image: url("https://4kwallpapers.com/images/wallpapers/dark-background-abstract-background-network-3d-background-4480x2520-8324.png");
    background-attachment: fixed;

}

.menu-image{
    width: 150px;
    height: 150px;
}

header{
    background-color: rgb(35, 35, 35);
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
}

nav{
    display: flex;
    flex-direction: row;
    gap: 15px;
    align-items: center;
    justify-content: center;
}

.homepic>img{
    width: 700px;
}

.details{
    text-align: left;
    color: #000;
    background: var(--background);
    padding: 5px;
}

/* button{
    background-color: white;
    color: black;
    width: 100px;
    height: 40px;
    font-size: 18px;
    font-weight: bold;
    border: 0;
    border-radius: 10px;
} */

/* Content-Page */ 
#content-frame {

    width: 50vw;
    /* height: calc(100vh - 50px); */
    /* text-align: center; */
    margin-left: 25%;
    padding: 5px;
    border-radius: 1rem;
    overflow: visible;
    background: #f7ba2b;
    background: var(--background);
    position: relative;
    z-index: 1;
   }
   
   #content-frame::after {
    position: absolute;
    content: "";
    top: 30px;
    left: 0;
    right: 0;
    z-index: -1;
    height: 100%;
    width: 100%;
    transform: scale(0.8);
    filter: blur(25px);
    background: #f7ba2b;
    background: var(--background);
    transition: opacity .5s;
   }
   
   #content-bg {
    --color: #000000;
    background: var(--color);
    color: var(--color);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    overflow: visible;
    border-radius: .7rem;
   }
   
   #content-frame #content {
    letter-spacing: .1em;
    text-align: center;
    color: white;
    padding: 10px;
   }
   
   /*Hover*/
   #content-frame:hover::after {
    opacity: 0;
   }
   
   #content-frame:hover #content-bg {
    color: #f7ba2b;
    transition: color 1s;
   }
   

   /* Button */ 
button {
    height: 50px;
    margin: 5px;
    width: 120px;
    background: #333;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    cursor: pointer;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    font-family: Consolas, Courier New, monospace;
    border: solid #404c5d 1px;
    font-size: 18px;
    font-weight: bold;
    color: rgb(255, 255, 255);
    -webkit-transition: 500ms;
    transition: 500ms;
    border-radius: 5px;
    background: linear-gradient(145deg, #2e2d2d, #212121);
    -webkit-box-shadow: -1px -5px 15px #41465b, 5px 5px 15px #41465b,
      inset 5px 5px 10px #212121, inset -5px -5px 10px #212121;
    box-shadow: -1px -5px 15px #41465b, 5px 5px 15px #41465b,
      inset 5px 5px 10px #212121, inset -5px -5px 10px #212121;
  }
  
  button:hover {
    -webkit-box-shadow: 1px 1px 13px #20232e, -1px -1px 13px #545b78;
    box-shadow: 1px 1px 13px #20232e, -1px -1px 13px #545b78;
    color: #d6d6d6;
    -webkit-transition: 500ms;
    transition: 500ms;
  }
  
  button:active {
    -webkit-box-shadow: 1px 1px 13px #f7ba2b, -1px -1px 33px #545b78;
    box-shadow: 1px 1px 13px #20232e, -1px -1px 33px #f7ba2b;
    color: #f7ba2b;
    -webkit-transition: 100ms;
    transition: 100ms;
  }
